<template>
  <main class="main">
    <div class="banner" style="background-image: url(/static/testimg/in_banner.jpg);"></div>
    <main_nav></main_nav>
    <div class="news-lists content">
      <div class="pt50 pb50 mauto ov clearfix">
        <div class="sliders">
          <div v-for="news_list1 in news_list1">
            <router-link :to="'/NewsInfo/' + news_list1.id" class="dfcc img-md sm-db">
              <div class="imgs sm-12 sm-ha">
                <img src="/static/testimg/banner.jpg" alt="">
              </div>
              <div class="info-w clearfix sm-12">
                <div class="date tac fl">
                  <em class="db mb5">29</em>
                  <span class="db">18-06</span>
                </div>
                <div class="info fl">
                  <h3 class="els f16 mb5">{{news_list1.title}}</h3>
                  <div class="els2 li25">{{news_list1.body}}</div>
                </div>
              </div>
            </router-link>
          </div>
        </div>
        <ul class="list hsms clearfix">
          <li class="lg-6 sm-12" hsm="fadeup" v-for="news_list2 in news_list2">
            <router-link :to="'/NewsInfo/' + news_list2.id" class="dfcc">
              <div class="date fl dfccs">
                <em class="db">29</em>
                <span>18-06</span>
              </div>
              <div class="info fl">
                <h3 class="els tra">【ID:{{news_list2.id}}】{{news_list2.title}}</h3>
                <div class="els2 li25">{{news_list2.body}}</div>
              </div>
            </router-link>
          </li>
        </ul>
        <!--Page-->
        <div class="in-page tac pt20">
          <a href="">| &lt;</a>
          <a href="">&lt;&lt;</a>
          <a href="" class="cur">1</a>
          <a href="">2</a>
          <a href="">3</a>
          <a href="">&gt;&gt;</a>
          <a href="">| &gt;</a>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      news_list1:[],
      news_list2:[],
    }
  },
  created() {
    csss();
    // resource -- 请求
    // this.$http.get("http://jsonplaceholder.typicode.com/posts").then(function(data) {
    //   this.news_list = data.body.slice(0, 10);
    // });
    axios.get(this.JsonUrl()).then(data => (
      this.news_list1 = data.data.slice(0, 3)
    ));
    axios.get(this.JsonUrl()).then(data => (
      this.news_list2 = data.data.slice(0, 10)
    ));    
  },
  mounted() {
    setTimeout(function(){
      sliders(".sliders",1);
    },500)

    // sliders(".list",2)
  },
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">

</style>



