<template>
  <main class="main">
    <div class="banner" style="background-image: url(/static/testimg/in_banner.jpg);"></div>
    <main_nav></main_nav>
    <div class="news-info content" style="background: #f3f3f3;">
      <div class="pt50 pb50 mauto ov clearfix">
        <div class="baf" hsm="fadeup">
          <div class="rel">
            <router-link to="/News" class="return">返回列表</router-link>
            <div class="tt1 tac">
              <h3 class="f24 fb pt35">{{News_Info.title}}</h3>
              <span class="pt10 pb30">时间：2017-11-30 来源：市场部</span>
            </div>
            <div class="news-share">
              <span class="fl">分享:</span>
              <div class="fl bdsharebuttonbox bdshare-button-style0-16" data-bd-bind="1501606084965">
                <a class="a1" href="" data-cmd="weixin" title="分享到微信"></a>
                <a class="a2" href="" data-cmd="tsina" title="分享到新浪微博"></a>
                <a class="a3" href="" data-cmd="tqq" title="腾讯微博"></a>
              </div>
            </div>
          </div>
          <div class="pl35 pr35">
            <div class="edit li30 f16 c6 pt15 pb15">
              {{News_Info.body}}
              <strong class="db tac">此条ID：{{News_Info.id}}</strong>
            </div>
            <div class="b-con pt20 pb20 clearfix">
              <div class="page lg-6 sm-12">
                <div><strong>上一条：</strong><a href="" class="els">上一条新闻</a></div>
                <div><strong>下一条：</strong><a href="" class="els">下一条新闻</a></div>
              </div>
              <div class="lg-6 pt10 sm-12 sm-p0 clearfix">
                <router-link to="/News" class="b-return fr sm-dn">返回列表</router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      id:this.$route.params.id,
      News_Info: {},
    }
  },
  created() {
    axios.get(this.JsonUrl() + "/" + this.id).then(data => (
      this.News_Info = data.data      
    ));
  },
  mounted() {
    
  },
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">

</style>



